body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f4f0f8;
}

h1 {
  color: #6f42c1; /* Lavender color */
}

select {
  margin: 10px 0;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #6f42c1; /* Lavender header */
  color: white;
}

tr:nth-child(even) {
  background-color: #f8f4fc; /* Very light lavender for even rows */
}

tr:hover {
  background-color: #e9d8f5; /* Light lavender on hover */
}
